import React from "react"
import { Link } from "gatsby";
//import HeaderTree from "./headerTree"
//import Footer from "./footer"

export default ({ children }) => {

  /*
  const onTouchStart = ()=>{


  }

  const onTouchEnd = ()=>{


  }
  */

  //:class="pageClasses"
  //<img src="/favicon-96x96.png" height="42" className="pl-3 d-md-none"/>
  return (
    <header className="cd-header">


      <ul className="cd-header-nav">
        <li className="cd-nav-item">

        </li>
        <li className="cd-nav-item d-sm-down-none">
          <Link
            to="/introduction"
            className="cd-nav-link"
          >
            Getting Started
          </Link>
        </li>
        <li className="cd-nav-item d-sm-down-none">
          <Link
            to="/components/CAlert"
            className="cd-nav-link"
          >
            Components
          </Link>
        </li>
        <li className="cd-nav-item d-sm-down-none">
          <a
            href="https://github.com/coreui/coreui-react"
            className="cd-nav-link cd-signalize"
            target="_blank"
          >
            Github
          </a>
        </li>
        <li className="cd-nav-item">
          <a
            href="https://coreui.io/react"
            className="cd-btn-go-pro cd-btn-coreui"
            target="_blank"
          >
            Go pro
          </a>
        </li>
        <button className="cd-header-toggler cd-class-toggler" type="button"></button>
      </ul>

    </header>
  )

}

